       
$(document).ready(function(){

    let items = $(".ready-pack__item");
    items.each(function(index){
        let parent = ($(this).children(" .ready-pack__mid"));
        let ul = parent.find(".ready-pack__mid-list") 
        if(ul.find("li").length > 5){
            let el = ul.find("li").slice(5, 100)
            parent.find(".ready-pack__switch").css("display", "grid");
            el.addClass("hidden");
            let open = (parent.find(".ready-pack__open"));
            let close = (parent.find(".ready-pack__close"));
            let present = (parent.find(".predloj__present"));


            open.on("click", function(){
                console.log("heya");
                el.removeClass("hidden");
                close.css("display", "flex")
                open.css("display", "none")
                present.css("opacity", "0");
            })

            close.on("click", function(){
                el.addClass("hidden");
                close.css("display", "none")
                open.css("display", "flex")
                present.css("opacity", "1");

            })
        }
    })
    
});